/*base styles*/
    *{
        box-sizing: border-box;
    }

    html, body{
        padding: 0;
        margin: 0;
        height: 100%;
        width: 100%;
        font-family: 'Roboto', sans-serif;
        font-size: 14px;

    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
    }

    .wrapper{
        position: relative;
        height: 100%;
        width: 100%;
        overflow-x: hidden;
    }

    .section{
        height: 100%;
        width: 100%;

        &__wrapper{
            position: relative;
            height: 100%;
            width: 100%;

            display: flex;
            align-items: center;

            @include smallmonitor{
                justify-content: center;
            }
        }
    }

    .container{
        width: 960px;
        max-width: 100%;
        height: 100%;
        margin: 0 auto;

        @include smallmonitor{
            padding: 0 5%;
        }
    }

    .title-label{
        display: inline-block;
        position: absolute;
        top: 80px;
        left: 50%;
        transform: translateX(-50%);

        color:  #ffffff;
        font-size: 23px;/* Приближение из-за подстановки шрифтов */
        font-weight: 600;

        &--bottom{
            top: inherit;
            bottom: 50px;
        }
    }

    .content{
        color:  #ffffff;
        font-size: 14px;/* Приближение из-за подстановки шрифтов */
        font-weight: 400;

        &__fw700{
            font-weight: 700;

            &--grey{
                color:  #b7b7b7;
                font-size: 23px;
                display: block;
                text-transform: uppercase;
            }

            &--white{
                font-size: 63px;
                text-transform: uppercase;
            }
        }

        &__descr{
            line-height: 23px;
            color:#bab8ac;
        }

        &__title{
            margin-right: 60px;
        }

        &__label{
            display: inline-block;
            color:  #ffffff;
            font-size: 23px;/* Приближение из-за подстановки шрифтов */
            font-weight: 300;

            border: 1px solid #fff;
            padding: 5px 15px;

            margin-bottom: 25px;
        }
    }
    
    .title{
        color:  #000000;
        font-size: 30px;/* Приближение из-за подстановки шрифтов */
        font-weight: 300;
    }
/*base end*/

/************************************/

/*intro*/
.intro{
    background: url(../img/intro-bg.png) no-repeat;
    background-position: center center;
    background-size: cover;

    &__content{
        display: inline-block;
        width: 50%;
    }
}

/*intro end*/

/**************************/

/*gallery*/

.gallery{

    &__wrapper{
        padding: 50px 0;
    }

    &__content{
        margin-bottom: 55px;

        .content{
            display: flex;
        }
    }

    &__picture{
        display: flex;
        justify-content: center;
        width: 100%;

        &-img{
            max-width: 100%;
        }
    }
}

/*gallery end*/

/******************************/

/*banner*/

.banner{
    background: url(../img/banner.png) no-repeat;
    background-position: center center;
    background-size: cover;
    padding: 96px 0;

    &__content{
        text-align: center;
        padding: 0 20%;
    }
}

/*banner end

/**********************************/

/*statistics*/

.statistics{
    margin: 50px 0;

    &__wrapper{
        text-align: center;
    }

    &__title{
        margin-bottom: 70px;
    }

    &__content{
        text-align: left;
    }

    .content__title{
        margin-right: 0;
        margin-bottom: 20px;
    }

    .content__descr{
        color: #000000;
    }
}

/*statistics end*/

/*******************************/

/*video*/
.video{
    background: url(../img/video-bg.png) no-repeat;
    background-position: center center;
    background-size: cover;
    position: relative;

    &__wrapper{
        padding: 100px 0 150px 0;
    }

    &__content{
       text-align: center;
       margin-bottom: 100px;
    }

    &__player{
        margin: 0 auto;
        width: 780px;
        height: 440px;

        @include phones{
           height: 250px; 
        }
    }
}

.player{
    max-height: 100%;
    max-width: 100%;
    background-color: black;
    box-shadow: 0 0 5px 10px black;
    position: relative;

    &:before{
        content:'';
        display: inline-block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border: 20px solid transparent;
        border-left: 25px solid white;
        border-radius: 6px;
        cursor: pointer;
    }
}

/*video end*/

/*******************************/

/*info*/
.info{
    padding: 10px 0;
    text-align: center;
    color: #000000;

    background-color: #f5f5f5;

    &__list{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__item{
        margin-right: 100px;

        &:last-child{
            margin-right:0;
        }

        &-value{
            font-size: 28px;/* Приближение из-за подстановки шрифтов */
            font-weight: 500;
            margin-bottom: 7px;
        }

        &-text{
            font-size: 17px;/* Приближение из-за подстановки шрифтов */
            font-weight: 400;
        }

        @include phones{
            margin-right: 10px;
        }
    }
}

/*info end*/
